import React, {Component} from 'react';
import {Container, Row, Col, Form, Button} from 'react-bootstrap';


function makeBasicPoint(han, fu) {
    if (fu !== 25) {
        fu = Math.ceil(fu / 10) * 10; //除非25符（七对子），把符无条件进位到十位
    }
    if (han > 13) han = 13; //累计役满
    if (fu < 20 || fu > 110) return 0;
    var base;
    switch (han) {
        case 13:
            return 8000; //累计役满
        case 12:
        case 11:
            return 6000; //三倍满
        case 10:
        case 9:
        case 8:
            return 4000; //倍满
        case 7:
        case 6:
            return 3000; //跳满
        case 5:
            return 2000; //满贯
        case 4:
        case 3:
        case 2:
        case 1:
            base = fu * Math.pow(2, han + 2); //基本公式
            return base > 2000 ? 2000 : base; //满贯判断
        default:
            return 0;
    }
}

function kiriage(value) {
    return Math.ceil(value / 100) * 100; //无条件进位至百位
}

class Dice extends Component {
    constructor() {
        super();
        this.state = {
            dice1: Dice.throwSingle(),
            dice2: Dice.throwSingle(),
        };
    }

    static throwSingle() {
        return Math.floor(Math.random() * 5) + 1
    }

    rethrow() {
        this.setState({
            dice1: Dice.throwSingle(),
            dice2: Dice.throwSingle(),
        })
    }

    render() {
        let diceResult = this.state.dice1 + ", " + this.state.dice2;
        let sum = this.state.dice1 + this.state.dice2;
        let start = "";

        switch (sum % 4) {
            case 0:
                start = "东";
                break;
            case 1:
                start = "南";
                break;
            case 2:
                start = "西";
                break;
            case 3:
                start = "北";
                break;
            default:
        }
        return (
            <div>
                <p>{diceResult}</p>
                <p>{start + sum}</p>
                <Button
                    variant="primary"
                    onClick={() => this.rethrow()}>再掷一组</Button>
            </div>
        );

    }
}


function Result(props) {
    let error = "";
    //因为翻>=5的话不用看符也即可算出点数，这个时候就不做出错处理了
    if (props.han < 5) {
        if (props.fu < 20 || props.fu > 110 || props.fu % 2 !== 0) {
            error += "\n符应位于区间 [20,110] 且为偶数（除25符七对子外）";
        }
        if (props.han < 1) {
            error += "\n翻应大于等于 1";
        }
        if (props.fu === 25) {
            if (props.han < 1) {
                error = "25符七对子时翻应大于等于 2"
            } else {
                error = "";
            }
        }
    }
    if (error === "") {
        return (
            <div className="result">
                <p>{props.fu === 25 ? 25 : Math.ceil(props.fu / 10) * 10}符 {props.han}翻 {props.mangan}</p>
                <p>基本点为 {props.basic}</p>
                <p>庄家</p>
                <p>荣和 {kiriage(props.basic * 6)}，自摸 {kiriage(props.basic * 2)} ALL
                    = {kiriage(props.basic * 2) * 3}</p>
                <p>闲家</p>
                <p>荣和 {kiriage(props.basic * 4)}，自摸 {kiriage(props.basic)},{kiriage(props.basic * 2)}
                    &nbsp;= {kiriage(props.basic) * 2 + kiriage(props.basic * 2)}</p>
            </div>
        );
    }
    return (
        <div>
            您的输入有误：<br/>
            {error}
        </div>
    );
}

class Board extends Component {
    constructor() {
        super();
        this.state = {
            han: 4,
            fu: 30,
        }
    }

    handleChange(e) {
        let id = e.target.id;
        let value = parseInt(e.target.value);
        if (isNaN(value) || typeof (value) == "undefined") return;
        console.log(id + " " + value);
        this.setState({[id]: value});
    }

    makeTextBox(id, value) {
        return <Form.Control
            type="text"
            id={id}
            defaultValue={value}
            onChange={(e) => this.handleChange(e)}
        />;
    }


    render() {
        let han = this.state.han;
        let fu = this.state.fu;
        let basic = makeBasicPoint(han, fu);
        let mangan = "";
        switch (basic) {
            case 2000:
                mangan = "满贯";
                break;
            case 3000:
                mangan = "跳满";
                break;
            case 4000:
                mangan = "倍满";
                break;
            case 6000:
                mangan = "三倍满";
                break;
            case 8000:
                mangan = "累计役满";
                break;
            default:
        }
        return (
            <div>
                <Container>
                    <Row>
                        <h1>日本麻将工具集</h1></Row>
                    <Row>
                        <Col xs={12} md={6} xl={4}>
                            <h2>快速点数计算</h2>
                            <Form>
                                <Row><Col xs={6} md={6}>
                                    <Form.Label>符数</Form.Label>
                                    {this.makeTextBox("fu", fu)}
                                </Col><Col xs={6} md={6}>
                                    <Form.Label>翻数</Form.Label>
                                    {this.makeTextBox("han", han)}
                                </Col></Row>
                            </Form>
                            <Result
                                fu={fu}
                                han={han}
                                mangan={mangan}
                                basic={basic}
                            />
                        </Col>
                        <Col xs={12} md={6} xl={4}>
                            <h2>模拟骰子</h2>
                            <Dice/>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Board;